import React from 'react';
import './Tournaments.css';

import { GiSoccerBall } from 'react-icons/all';
import Tournament from '../ui/Tournament';

const Tournaments = () => {

    const tournaments = [
        {
            id: 1,
            title: "Silver Pirates Top 8 (u23)",
            firstPrize: "R12000",
            secondPrize: "R6000",
            joiningFee: "R1000",
            description: "The tournament aims to entertain our people during the holidays time",
            startDate: "12-12-2021",
            endDate: "18-12-2021",
        } 
        ,{
            id: 12,
            title: "Central City Top 16",
            firstPrize: "R10000",
            joiningFee: "R2000",
            secondPrize: "R6000",
            description: "The tournament aims to entertain our people during the holidays time",
            startDate: "12-12-2021",
            endDate: "18-12-2021",
        } 
        ,{
            id: 21,
            title: "Young Brothers Good Friday Top 8",
            firstPrize: "R12000",
            joiningFee: "R700",
            secondPrize: "R6000",
            description: "The tournament aims to entertain our people during the holidays time",
            startDate: "12-12-2021",
            endDate: "18-12-2021",
        } 
        
    ];



    return (
        <div className="tournaments margin-top">
            <div className="tournaments-header">
                <h3><GiSoccerBall className="icon" /> Tournaments</h3>
                <small>All the public tournaments for you to join</small>
            </div>
            <div className="tournament-buttons">
                <button >
                    All
                </button>

                <button >
                    Prev
                </button>

                <button className="active">
                    Upcoming
                </button>
            </div>
            <div className="tournaments-list">
                {
                    tournaments.map(tournament => (
                        <Tournament key={tournament.id} tournament={tournament}/>
                    ))
                }
            </div>
        </div>
    )
}

export default Tournaments;
