import React from 'react';
import { BiCalendar, BiMedal, BiMoney, BiTrophy } from 'react-icons/bi';
import { Link } from 'react-router-dom';
import Button from '../controls/Button';

const Tournament = ({tournament}) => {

    const {id, title, description, firstPrize, joiningFee, secondPrize, startDate, endDate } = tournament;
    
    return (
        <Link className="tournament" to={`/tournaments/${id}`}>
            <h2>{title}</h2>
            <p>{ description }...</p>
            <h5>Details:</h5>
            <div className="px-3">
                <h6> <BiMoney className="icon" /> Joining Fee: { joiningFee }</h6>
                <h6> <BiTrophy className="icon" /> Winner: { firstPrize }</h6>
                <h6> <BiMedal className="icon" /> 2nd: { secondPrize }</h6>
                <div className="tournament-date">
                    <h4> <BiCalendar className="icon" /> {startDate } - {endDate}</h4>
                </div>
                <div className="join">
                    {/* <Button text="join" /> */}
                </div>
            </div>
        </Link>
    );
};

export default Tournament;