import React from 'react';
import { BiCalendar, BiMedal, BiMoney, BiTrophy } from 'react-icons/bi';
import { GiSoccerBall } from 'react-icons/gi';
import { SiGooglemaps } from 'react-icons/si';

import { useParams } from 'react-router-dom';
import Button from '../controls/Button';

const TournamentDetails = () => {

    const { id } = useParams();

    return (
        <div className="tournament-details">
            <div className="details">
                <h3><GiSoccerBall className="icon" /> Silver Pirates Top 8 { id }</h3>
                <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Sed, error perferendis?</p>
                <h4>Details</h4>
                <div className="px-3 prize">
                    <h6> <BiMoney className="icon" /> Joining Fee: { "R100" }</h6>
                    <h6> <BiTrophy className="icon" /> Winner: { "firstPrize" }</h6>
                    <h6> <BiMedal className="icon" /> 2nd: { "secondPrize" }</h6>
                    <div className="tournament-date">
                        <h6> <BiCalendar className="icon" /> {"startDate" } - {"endDate"}</h6>
                    </div>
                </div>
                <hr />

                <h4>Location</h4>
                <div className="location px-3">
                    <h6>Young Brothers</h6>
                    <h6>Limpopo</h6>
                    <h6>Polokwane</h6>
                    <h6>0200</h6>
                    <a href=""><SiGooglemaps className="icon" /> Open Google Maps</a>
                </div>
            </div>
            
            <div className="join-tournament">
                <small>By joining the tournament means you agree to the teams and conditions <span><a href="/">Read more...</a></span></small>
                <input type="checkbox"/><span>Agree?</span>
                <Button text="join tournament" />
            </div>
        </div>
    );
};

export default TournamentDetails;